// antd 菜单栏下拉样式
.nav-common-sub.ant-menu-dark {
  // 下拉样式
  .ant-menu-sub {
    background: #404041;
    // 标题
    .ant-menu-item-group-title {
      position: relative;
      color: #fff;
      background: #404041; //2a2a2a
      overflow: hidden;
      // border-bottom: 1px dashed #aaa;
      // 虚线
      // background-image: linear-gradient(to right, #aaa 0%, #aaa 50%, transparent 50%);
      // background-size: 11px 1px;
      // background-repeat: repeat-x;
      // background-position: 10px bottom;
      .text{
        position: relative;
        display: inline-block;
        padding: 0 10px 0 10px;
        background: #404041;
        z-index: 2;
      }
      .line::before{
        content: '';
        position: absolute;
        width: 9999px;
        height: 1px;
        background: #aaa;
        top: 50%;
        left: -50px;
        z-index:1;
      }
    }
    // 下拉菜单标题
    .ant-menu-submenu-title {
      background: #404041; //2a2a2a
    }
    .ant-menu-item {
      // background:#191919;
    }
    .ant-menu-item:hover{
      color:#47a3da;
      background: #2f2f2f;
    }
    .ant-menu-item-selected{
      background: #404041;
      color: rgba(255, 255, 255, 0.65);
    }
    li {
      border-bottom: #000;
    }
    &.ant-menu-sub {
      -webkit-box-shadow: 0 2px 8px rgba(25, 25, 25, 0.45) inset;
      box-shadow: 0 2px 8px rgba(25, 25, 25, 0.45) inset;
    }
  }
}
// 代码下拉显示样式
.code-collapse{
  margin-top: 6px;
  color: #e2e2e2;
  .ant-collapse{
    border-radius: 0;
    background-color:#404041;
    border: 1px solid #a0a0a0;
  }
  .ant-collapse > .ant-collapse-item {
     border-bottom: none;
  }
  .ant-collapse > .ant-collapse-item:last-child, .ant-collapse > .ant-collapse-item:last-child > .ant-collapse-header {
      border-radius: 0 !important;
  }
  .ant-collapse-item:last-child > .ant-collapse-content {
      border-radius: 0 !important;
  }
  .ant-collapse-content > .ant-collapse-content-box {
      padding: 6px;
  }
  .ant-collapse-content{
      background-color:#404041;
  }
  .code-panel{    
    .ant-collapse-header{
      // position: absolute !important;
      border-radius:0 !important;
      padding: 0 !important;
      margin: 0 !important;
      cursor: auto !important;
      color: #e2e2e2 !important;
      .box{
        height: 30px;
        line-height: 30px;      
      }
      // right: 0;
      // top:0;
    }
    .ant-collapse-extra{
      float: none !important;
      position: absolute;
      top: 0;
      right: 0;
      .ant-btn-link{
        color: #e2e2e2;
        padding: 0 10px;
        &:hover{
          color: #47a3da;
        }
      }
    }
    // .ant-collapse-content{
    //   height: 100%;
    // }
  }
  .ant-collapse-item{
    position: relative;
  }  
}
