@import './antd-common.scss';

.antd-page {
  a {
    color: #e2e2e2;
  }
  a:hover {
    color: #000;
  }  
  .code-box{
    // max-height: 220px;
    // overflow: auto;
    position: relative; 
    pre{
      padding: 0 !important;
      margin: 0 !important;
      max-height: 300px !important;
      overflow: initial !important;
    }
    code{
      color: #f3f3f3 !important;
      background: #404041 !important;
      box-shadow:none !important;
      padding: 6px 32px !important;
    }
  }
}

